button {
  background: linear-gradient(#013e92, #3d1984bf);
  color: #eee;
  margin-left: 1rem;
}
button:hover {
  background: linear-gradient(#3d1984bf, #013e92);
  box-shadow: 0 0 5px 0px #033f92;
  color: #fff;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
}
.addPost {
  margin: 1rem 0;
}
.addPost textarea {
  display: flex;
  border-radius: 1rem 0 0 1rem;
}
.addPost button {
  display: flex;
  align-self: normal;
  margin: 0;
  border-radius: 0 1rem 1rem 0;
}
