.img {
  display: flex;
  align-items: center;
  max-width: 100px;
  width: 100%;
  height: 100px;
  overflow: hidden;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #eee;
  margin: 1rem;
}
.message {
  display: flex;
  padding: 2.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 5px 0px;
  position: relative;
}
.message::before,
.message::after {
  content: "";
  left: -1.5rem;
  border: 0.7rem solid transparent;
  border-right: 0.7rem solid;
  position: absolute;
}
.message::after {
  border-right: 0.7rem solid #ebfbfd;
  left: -1.4rem;
}
.message p {
  margin: 0 1rem;
  word-break: break-all;
}
.img img {
  width: 100%;
  height: auto;
}
.like img {
  width: 32px;
}